@charset "UTF-8";
:root {
  font-size: 16px;
  box-sizing: border-box;
}

@font-face {
  font-family: "Space Mono";
  src: url("../fonts/SpaceMono-BoldItalic.woff2") format("woff2");
  font-weight: bold;
  font-style: italic;
}
@font-face {
  font-family: "Space Mono";
  src: url("../fonts/SpaceMono-Bold.woff2") format("woff2");
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: "Space Mono";
  src: url("../fonts/SpaceMono-Regular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Space Mono";
  src: url("../fonts/SpaceMono-Italic.woff2") format("woff2");
  font-weight: normal;
  font-style: italic;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/poppins-latin-300-normal.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/poppins-latin-500-normal.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
}
h1 {
  font-family: "Space Mono";
  font-size: clamp(27.588px, 5vw + 1rem, 96px);
  font-style: italic;
  font-weight: bold;
  line-height: normal;
  text-transform: uppercase;
}

h2 {
  font-family: "Space Mono";
  font-size: 66px;
  font-style: italic;
  font-weight: normal;
  line-height: 58px;
  text-transform: uppercase;
}

h3 {
  font-family: "Space Mono";
  font-size: 18px;
  font-style: normal;
  font-weight: normal;
  line-height: normal;
  letter-spacing: 1.8px;
  text-transform: uppercase;
}

:root {
  --color-main-bg: #ffffff;
  --color-main-text: #000000;
  --color-modal-bg: #e00000;
  --color-modal-text: #ffffff;
  --color-button-primary-bg: #d8d8d8;
  --color-button-primary-text: #000000;
  --color-button-hover-bg: #000000;
  --color-button-hover-text: #ffffff;
  --Blues-Gray2: #B8BBC2;
  --color-form-bg-default: #ffffff;
  --color-form-bg-hover: #ffd6d6;
  --color-form-bg-active: #fe5858;
  --color-form-bg-selected: #e00000;
  --color-form-text-default: #000000;
  --color-form-text-secondary: #313144;
  --color-form-text-selected: #ffffff;
}

/* ==========================================================================
   GLOBAL STYLES
   ========================================================================== */
html, body {
  margin: 0;
  min-height: 100%;
  font-family: "Space Mono", monospace;
}

/* Utility Classes */
.hidden {
  display: none !important;
}

/* Layout */
.main-container {
  position: relative;
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0;
  background-color: var(--color-main-bg);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.site-content {
  flex: 1;
}

.main-container::before {
  z-index: 1;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  opacity: 0;
  transition: opacity 0.5s ease-out;
  pointer-events: none;
}

.main-container.overlay-active::before {
  opacity: 1;
}

/* ==========================================================================
   MASTHEAD STYLES
   ========================================================================== */
#masthead {
  position: relative;
  top: 0;
  z-index: 1;
  height: 5rem;
  background: #FFF;
  box-shadow: 0px 4px 14px 10px rgba(0, 0, 0, 0.03);
}

#masthead .logo-menu-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  background: #FFF;
}

#masthead .logo-menu-wrapper .logo-container {
  padding-left: clamp(1rem, 5vw + 2rem, 9.125rem);
}

#masthead .logo-menu-wrapper .logo-container img {
  height: auto;
  width: 13.5rem;
}

/* ==========================================================================
   PRIMARY NAVIGATION STYLES
   ========================================================================== */
#primary-navigation {
  display: flex;
}

#primary-navigation .main-menu {
  display: flex;
  list-style: none;
  margin: 0;
  padding-right: clamp(1rem, 5vw + 2rem, 9.125rem);
  gap: 2.8125rem;
}

#primary-navigation .main-menu li a {
  text-decoration: none;
  text-transform: uppercase;
  color: var(--color-main-text);
  transition: font-weight 0.3s ease;
}

#primary-navigation .main-menu li a:hover {
  font-weight: bold;
  text-decoration: none;
}

/* ==========================================================================
   MENU BUTTON STYLES
   ========================================================================== */
.menu__button {
  position: absolute;
  right: 40.6px;
  display: none;
  justify-content: center;
  align-items: center;
  width: 27.4px;
  height: 18.8px;
  padding: 0;
  background: transparent;
  border: none;
  cursor: pointer;
}

.menu__button--line,
.menu__button--line::before,
.menu__button--line::after {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 2px;
  border-radius: 1px;
  background-color: #000;
  transition: transform 0.3s ease, opacity 0.3s ease;
}

.menu__button--line::before {
  top: -9.4px;
}

.menu__button--line::after {
  top: 9.4px;
}

/* ==========================================================================
   ANIMATIONS
   ========================================================================== */
@keyframes openMenu {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0);
  }
}
@keyframes closeMenu {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(100%);
  }
}
.nav-active {
  animation: openMenu 0.8s ease-out forwards;
}

.menu-closing {
  animation: closeMenu 0.5s ease-out forwards;
}

/* ==========================================================================
   MEDIA QUERIES - MASTHEAD
   ========================================================================== */
@media (max-width: 768px) {
  #masthead {
    position: sticky;
  }
  #masthead .logo-menu-wrapper .logo-container img {
    width: 8.625rem;
  }
  #primary-navigation {
    display: none;
  }
  #primary-navigation.menu-active {
    display: flex;
    position: absolute;
    top: 100%; /* Position the menu right below the parent */
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: var(--color-modal-bg);
    justify-content: center;
  }
  #primary-navigation.menu-active .main-menu {
    flex-direction: column;
    text-align: center;
    margin-top: 18rem;
    gap: 1.125rem;
  }
  #primary-navigation.menu-active .main-menu li a {
    font-size: 2.75rem;
    color: var(--color-modal-text);
  }
  #primary-navigation.menu-active .main-menu li a:hover {
    font-weight: normal;
    text-decoration: none;
  }
  .menu__button {
    display: flex;
  }
  .menu__button.menu-active .menu__button--line {
    background-color: transparent !important;
  }
  .menu__button.menu-active .menu__button--line::before {
    transform: rotate(45deg) translate3d(7px, 4px, 0);
  }
  .menu__button.menu-active .menu__button--line::after {
    transform: rotate(-45deg) translate3d(8px, -6px, 0);
  }
}
/* ==========================================================================
   SITE CONTENT STYLES
   ========================================================================== */
.site-content {
  max-width: 100%;
  /* ==========================================================================
     HERO BANNER STYLES
     ========================================================================== */
}
.site-content .hero-banner {
  width: 100%;
  height: 100%;
  max-height: 966px;
  overflow: hidden;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.site-content .hero-banner__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.site-content .hero-banner__title {
  position: absolute;
  width: 100%;
  text-align: center;
  color: transparent;
  -webkit-text-stroke: 3px rgb(255, 255, 255);
}

/* ==========================================================================
   MEDIA QUERIES
   ========================================================================== */
@media (max-width: 768px) {
  .site-content .hero-banner__title {
    -webkit-text-stroke-width: 1px;
  }
}
/* ==========================================================================
   FILTER FORM STYLES
   ========================================================================== */
#filter-form {
  margin-top: 80px;
  margin-left: clamp(1rem, 5vw + 2rem, 9.125rem);
  margin-right: clamp(1rem, 5vw + 2rem, 9.125rem);
  display: flex;
  justify-content: space-between;
}

/* ==========================================================================
   SELECT2 CONTAINER STYLES
   ========================================================================== */
.select2-container {
  border-radius: 8px;
  border: 1.3px solid var(--Blues-Gray2);
}

.select2-container:nth-child(2),
.select2-container:nth-child(4) {
  flex: 0 0 22.3%;
}

.select2-container:nth-child(2) {
  margin-right: 18px;
}

.select2-container:nth-child(4) {
  margin-left: 18px;
  margin-right: 18px;
}

.select2-container:nth-child(6) {
  margin-left: auto;
  display: flex;
  justify-content: flex-end;
}

/* ==========================================================================
   SELECT2 SELECTION STYLES
   ========================================================================== */
#filter-form .select2-selection--single {
  display: flex;
  align-items: center;
  width: 16.25rem;
  height: 2.688rem;
  padding-right: 18px;
  border-radius: 8px;
  background-color: transparent;
  border: 1.3px solid transparent;
  font-family: Poppins, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  text-transform: uppercase;
  line-height: 18px;
  letter-spacing: 0.36px;
}

#filter-form .select2-selection__rendered {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  padding-left: 18px;
}

/* ==========================================================================
   ARROW ICON STYLES
   ========================================================================== */
.select2-container--default .select2-selection--single .select2-selection__arrow {
  display: inline-block;
  width: 35px !important;
  padding-right: 18px;
  text-align: center;
  border: none;
  align-items: center;
  justify-content: center;
  transition: none;
  top: unset !important;
}

.select2.select2-container .select2-selection .select2-selection__arrow b {
  background-image: url("../images/chevron-down-s.svg");
  background-color: transparent;
  background-size: contain;
  background-repeat: no-repeat;
  border: none !important;
  height: 20px !important;
  width: 20px !important;
  margin: auto !important;
  top: auto !important;
  left: auto !important;
}

.select2-container--open .select2-selection__arrow {
  transform: rotate(180deg) !important;
}

/* ==========================================================================
   SELECT2 OPEN AND SEARCH FIELD STYLES
   ========================================================================== */
.select2-search__field {
  display: none;
}

.select2-container--open {
  border: solid 1px #215aff !important;
}

.select2-container--open .select2-dropdown--below,
.select2-container--open .select2-dropdown--above {
  font-family: Poppins, sans-serif;
  font-style: normal !important;
  font-weight: 500;
  font-size: 12px;
  border: none !important;
}

/* ==========================================================================
   SELECT2 RESULT STYLES
   ========================================================================== */
.select2-results__option {
  padding: 10px !important;
}

.select2-results__option--highlighted:active {
  background-color: var(--color-form-bg-active) !important;
}

.select2-results__option--highlighted {
  background-color: var(--color-form-bg-hover) !important;
  color: #000 !important;
}

.select2-results__option--selected {
  background-color: var(--color-form-bg-selected) !important;
  color: #fff !important;
}

/* ==========================================================================
   RESPONSIVE STYLES
   ========================================================================== */
@media (max-width: 992px) {
  #filter-form {
    flex-direction: column;
    align-items: center;
    gap: 8px;
    margin-top: 28px;
    margin-bottom: 28px;
  }
  .select2-container:nth-child(2),
  .select2-container:nth-child(4),
  .select2-container:nth-child(6) {
    margin: unset;
  }
  .select2-container:nth-child(6) {
    display: block;
    justify-content: unset;
  }
}
/* ==========================================================================
   PHOTO CATALOG STYLES
   ========================================================================== */
.photo-catalog {
  margin-left: clamp(1rem, 5vw + 2rem, 9.125rem);
  margin-right: clamp(1rem, 5vw + 2rem, 9.125rem);
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-top: 3.25rem;
  gap: 20px;
  /* ==========================================================================
     MEDIA STYLES
     ========================================================================== */
}
.photo-catalog__media {
  flex: 1 1 calc(50% - 20px);
  min-width: 404px;
  max-width: 585px;
  aspect-ratio: 585/513;
  position: relative;
  display: inline-block;
}
.photo-catalog__media img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  max-width: 585px;
  max-height: 513px;
  display: block;
}
.photo-catalog__media:hover .photo-overlay {
  opacity: 1;
  visibility: visible; /* Show overlay on hover */
}

/* ==========================================================================
   SPECIAL CASES
   ========================================================================== */
.photo-catalog__media:nth-last-child(1):nth-child(odd) {
  flex: 0 0 49%;
  margin-right: auto;
}

/* ==========================================================================
   LOAD MORE BUTTON STYLES
   ========================================================================== */
#load-more-container #load-more {
  display: block;
  width: 15.125rem;
  height: 3.125rem;
  font-family: "Space Mono";
  margin: 2.5rem auto 0;
  text-align: center;
  padding: 0.5rem 0.938rem;
  background: var(--color-button-primary-bg);
  border-radius: 2px;
  cursor: pointer;
  border: 2px solid #fff;
  color: var(--color-button-primary-text);
  transition: background-color 0.8s ease, color 0.8s ease;
}
#load-more-container #load-more:hover {
  background-color: var(--color-button-hover-bg);
  color: var(--color-button-hover-text);
  font-weight: 700;
}

/* ==========================================================================
   MEDIA QUERIES
   ========================================================================== */
@media (max-width: 992px) {
  .photo-catalog {
    padding-top: 0;
    justify-content: center;
  }
}
@media (max-width: 576px) {
  .photo-catalog {
    margin-left: 1rem;
    margin-right: 1rem;
    justify-content: center;
  }
  .photo-catalog__media {
    flex: 1 1 calc(100% - 2rem); /* Single column layout */
    min-width: 300px;
    max-width: 100%;
  }
}
@media (max-width: 992px) {
  .photo-catalog__media:nth-last-child(1):nth-child(odd) {
    flex: 1 1 calc(50% - 20px);
    margin-right: unset;
  }
}
/* ==========================================================================
   PHOTO OVERLAY STYLES
   ========================================================================== */
.photo-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.6);
  box-sizing: border-box;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}

/* ==========================================================================
   TOP SECTION STYLES
   ========================================================================== */
.photo-overlay__top {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  padding-top: 11px;
  padding-right: 18px;
  box-sizing: border-box;
}

/* ==========================================================================
   CENTER SECTION STYLES
   ========================================================================== */
.photo-overlay__center {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* ==========================================================================
   BOTTOM SECTION STYLES
   ========================================================================== */
.photo-overlay__bottom {
  width: 100%;
  display: flex;
  justify-content: space-between;
  color: #fff;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 500;
}

/* ==========================================================================
   INFO SECTION STYLES
   ========================================================================== */
.photo-overlay__info {
  color: #fff;
  padding: 12px 17px;
  box-sizing: border-box;
}

/* ==========================================================================
   FOOTER
   ========================================================================== */
.site-footer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 31px;
  margin-top: 2.5rem;
  padding-top: 1px;
  padding-bottom: 8px;
  border-top: solid 1px var(--color-main-text);
}
.site-footer__navigation {
  align-self: center;
}
.site-footer__navigation--menu {
  display: flex;
  list-style: none;
  gap: 5.375rem;
  padding: 0;
  margin: 0;
}
.site-footer__navigation--menu .menu-item {
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  line-height: normal;
}
.site-footer__navigation--menu .menu-item a {
  color: var(--color-main-text);
  text-decoration: none;
}
.site-footer__navigation--menu .menu-item a:hover {
  font-weight: bold;
  text-decoration: none;
}

/* ==========================================================================
   MEDIA QUERIES
   ========================================================================== */
/* Media Queries - FOOTER
   ========================================================================== */
/* TABLET breakpoint */
@media (max-width: 768px) {
  .site-footer {
    height: 208px;
  }
  .site-footer__navigation--menu {
    text-align: center;
    flex-direction: column;
    gap: 2.25rem;
  }
}
/* ==========================================================================
   Contact Overlay
   ========================================================================== */
.contact-overlay {
  position: fixed;
  inset: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.8);
  z-index: 9999;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-overlay__container {
  width: 597px;
  overflow: hidden;
  border: solid var(--color-main-text);
  background-color: var(--color-main-bg);
}
.contact-overlay__container .fa-circle-xmark {
  display: none;
}
.contact-overlay__container .contact-overlay__header {
  width: 886px;
  margin-top: 18px;
}
.contact-overlay__container .contact-overlay__header .contact-overlay__header-wrapper {
  position: relative;
  right: 8.95rem;
}
.contact-overlay__container .contact-overlay__header .contact-overlay__header-wrapper:nth-of-type(2) {
  right: 6.5rem;
}

/* ==========================================================================
   Contact Form 7
   ========================================================================== */
.wpcf7-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.wpcf7-form p {
  margin-top: 24px;
  margin-bottom: 0px;
}
.wpcf7-form p:nth-of-type(1) {
  margin-top: 46px;
}
.wpcf7-form p > label {
  font-family: Poppins;
  font-size: 0.75rem;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
}
.wpcf7-form-control {
  width: 263px;
  height: 40px;
  border-radius: 8px;
  border: 1px solid #B8BBC2;
  background: #FFF;
}

form.wpcf7-form input[type=text],
form.wpcf7-form input[type=email] {
  margin-top: 3px;
  font-family: Poppins;
  font-size: 0.75rem;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

/* Textarea */
.wpcf7-textarea {
  height: 210px;
  resize: none;
}

/* Submit Button */
.wpcf7-submit {
  display: block;
  margin: 0 auto;
  margin-top: 18px;
  width: 263px;
  height: 50px;
  padding: 8px 15px;
  font-family: "Space Mono";
  font-size: 0.75rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  border: none;
  border-radius: 0px;
  background-color: var(--color-button-primary-bg);
}

.hidden {
  display: none;
}

.fade-in {
  animation: fadeIn 0.5s forwards;
}

.fade-out {
  animation: fadeOut 0.5s forwards;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(0%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeOut {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(0%);
  }
}
/* ========================================================================== */
/* Media Queries - CONTACT OVERLAY
========================================================================== */
@media (max-width: 768px) {
  .contact-overlay__container {
    width: 286px;
    height: 90vh;
  }
  .wpcf7-form-control {
    width: 190px;
  }
  .wpcf7-not-valid-tip,
  .wpcf7-response-output {
    font-size: 0.8em;
  }
  .contact-overlay__header--image {
    width: 145px;
  }
  .contact-overlay__container .contact-overlay__header {
    margin-top: 6px;
  }
  .contact-overlay__container .contact-overlay__header .contact-overlay__header-wrapper {
    right: 5.8rem;
  }
  .contact-overlay__container .contact-overlay__header .contact-overlay__header-wrapper:nth-of-type(2) {
    right: 4.4rem;
  }
  .contact-overlay__container .fa-circle-xmark {
    position: relative;
    top: 0.4%;
    left: 90%;
    display: inline-block;
    font-size: x-large;
    cursor: pointer;
  }
  .contact-overlay__container .fa-circle-xmark:active {
    filter: invert(1);
  }
}
/* ==========================================================================
   SINGLE ENTRY STYLES
   ========================================================================== */
.single-entry {
  display: flex;
  justify-content: space-between;
  padding-top: 38px;
  gap: 20px;
}
.single-entry-data, .single-entry-media {
  flex: 1;
  box-sizing: border-box;
}
.single-entry-data {
  margin-left: clamp(1rem, 5vw + 2rem, 9.125rem);
  align-self: flex-end;
  padding-bottom: 2.25rem;
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid var(--color-main-text);
  max-width: 50%;
}
.single-entry-data__title {
  margin-bottom: 1.25rem;
}
.single-entry-data__meta {
  list-style: none;
  padding: 0;
  margin: 0;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
  font-size: 14px;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 1.4px;
}
.single-entry-media {
  margin-right: clamp(1rem, 5vw + 2rem, 9.125rem);
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
}
.single-entry-media img {
  max-width: 35.188rem;
  width: 100%;
  height: auto;
  object-fit: cover;
}

/* ==========================================================================
   SINGLE CONTACT STYLES
   ========================================================================== */
.single-contact {
  height: 7.375rem;
  margin-left: clamp(1rem, 5vw + 2rem, 9.125rem);
  margin-right: clamp(1rem, 5vw + 2rem, 9.125rem);
  display: flex;
  align-items: center;
  border-bottom: 1px solid var(--color-main-text);
  gap: 20px;
}
.single-contact__btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 15.125rem;
  height: 2.125rem;
  padding: 0.5rem 0.938rem;
  background-color: var(--color-button-primary-bg);
  color: var(--color-main-text);
  font-size: 12px;
  text-decoration: none;
  transition: background-color 0.8s ease, color 0.8s ease;
  border-radius: 2px;
  box-shadow: 0px 2px 1px 0px rgba(0, 0, 0, 0.02);
}
.single-contact__btn:hover {
  background-color: var(--color-button-hover-bg);
  color: var(--color-button-hover-text);
  font-weight: 700;
}
.single-contact__CTA {
  width: 49%;
  display: flex;
  gap: 20px;
  justify-content: space-between;
  align-items: center;
  font-family: Poppins;
  font-size: 14px;
  font-weight: 300;
  line-height: normal;
}

/* ==========================================================================
   SINGLE NAVIGATION STYLES
   ========================================================================== */
.single-navigation {
  margin-left: auto;
}
.single-navigation .single-navigation__arrow-container {
  display: flex;
  gap: 30px;
}
.single-navigation .post-thumbnail-preview {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.8s ease;
}
.single-navigation .post-thumbnail-preview img {
  width: 5.063rem;
  height: 4.438rem;
  object-fit: cover;
}

/* ==========================================================================
   SINGLE RELATED PHOTOS STYLES
   ========================================================================== */
.single-related-photos {
  margin-left: clamp(1rem, 5vw + 2rem, 9.125rem);
  margin-right: clamp(1rem, 5vw + 2rem, 9.125rem);
  margin-top: 5.313rem;
}
.single-related-photos__container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-top: 3.25rem;
  gap: 20px;
}
.single-related-photos__media {
  margin-bottom: 6.813rem;
  flex: 1 1 calc(50% - 20px);
  min-width: 300px;
  max-width: 580px;
  aspect-ratio: 585/513;
  position: relative;
}
.single-related-photos__media img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  max-width: 585px;
  max-height: 513px;
}
.single-related-photos__media:hover .photo-overlay {
  opacity: 1;
  visibility: visible;
}

/* ==========================================================================
   RESPONSIVE STYLES FOR TABLETS AND LARGE PHONES
   ========================================================================== */
@media (max-width: 768px) {
  .single-entry {
    flex-direction: column-reverse;
    padding-top: 20px;
    width: 100%;
    border-bottom: 1px solid var(--color-main-text);
  }
  .single-entry-media {
    margin-right: clamp(1rem, 5vw + 2rem, 3.375rem);
    margin-left: clamp(1rem, 5vw + 2rem, 3.375rem);
  }
  .single-entry-media img {
    max-width: unset;
  }
  .single-entry-data {
    margin-right: clamp(1rem, 5vw + 2rem, 3.375rem);
    margin-left: clamp(1rem, 5vw + 2rem, 3.375rem);
    align-self: flex-start;
    border-bottom: none;
    max-width: unset;
  }
  .single-entry-data__title {
    margin-top: 0;
    font-size: 46px;
  }
  .single-contact {
    margin-left: 0;
    margin-right: 0;
    width: 100%;
    height: auto;
  }
  .single-contact__btn {
    width: 14.563rem;
    margin: 0 auto;
    margin-bottom: 1.438rem;
  }
  .single-contact__CTA {
    display: flex;
    flex-direction: column;
    width: auto;
    margin: 0 auto;
    gap: unset;
  }
  .single-contact__CTA p {
    align-self: flex-start;
  }
  .single-navigation {
    display: none;
  }
  .single-related-photos {
    margin-top: 1.5rem;
  }
  .single-related-photos__title {
    text-align: center;
  }
  .single-related-photos__container {
    padding-top: 0.125rem;
    flex-direction: column;
    flex-wrap: nowrap;
  }
  .single-related-photos__media {
    margin-bottom: 0rem;
    min-width: unset;
  }
  .single-related-photos__media img {
    width: 100%;
    height: auto;
    object-fit: cover;
    aspect-ratio: 585/513;
  }
  .single-related-photos__media:last-child {
    margin-bottom: 1.313rem;
  }
}
@media (max-width: 576px) {
  .single-related-photos__media img {
    aspect-ratio: 585/513;
  }
}
/* ==========================================================================
   LIGHTBOX STYLES
   ========================================================================== */
.lightbox {
  background-color: rgba(0, 0, 0, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  display: flex;
  justify-content: center;
  animation: lightboxIn 0.5s;
}

.lightbox__container {
  align-self: center;
}
.lightbox__container img {
  max-width: 80vw;
  max-height: 80vh;
}

.lightbox__info {
  display: flex;
  justify-content: space-between;
  color: #FFF;
}

/* ==========================================================================
   LIGHTBOX NAVIGATION BUTTONS
   ========================================================================== */
.lightbox__next,
.lightbox__prev {
  border: none;
  background-color: transparent;
  background-image: url("../images/lightbox-arrow-default.svg");
  background-position: center center;
  background-size: 27px 16px;
  background-repeat: no-repeat;
  width: 50px;
  height: 100%;
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  z-index: 11;
  transition: background-size 0.3s ease, background-image 0.3s ease, background-color 0.3s ease;
  text-align: center;
  color: transparent;
  display: flex;
  align-items: center;
}

.lightbox__prev {
  left: 9%;
  right: auto;
  transform: translateY(-50%) rotate(180deg);
}

.lightbox__next {
  right: 10%;
}

.lightbox__next:hover,
.lightbox__prev:hover {
  background-image: url("../images/lightbox-arrow-hover.svg");
  background-size: 36px 16px; /* Adjusting the size for the hover state */
  background-position: right center; /* Ensure the position stays consistent */
}

/* ==========================================================================
   NAVIGATION BUTTON LABELS
   ========================================================================== */
.lightbox__next::before,
.lightbox__prev::after {
  color: #FFF;
  font-family: "Space Mono";
  font-size: 12px;
  font-weight: 400;
  line-height: normal;
  position: absolute;
  z-index: 12;
  display: block;
}

.lightbox__next::before {
  content: "Suivante";
  left: -60px;
}

.lightbox__prev::after {
  content: "Précédente";
  transform: rotate(180deg);
  right: 55px;
}

.lightbox__next:hover::before,
.lightbox__prev:hover::after {
  font-weight: 700; /* Make the text bold on hover */
}

/* ==========================================================================
   LIGHTBOX CLOSE BUTTON
   ========================================================================== */
.lightbox__close {
  border: none;
  background-color: transparent;
  background-image: url("../images/lightbox-btn-close.svg");
  background-position: center center;
  background-size: 23px 23px;
  background-repeat: no-repeat;
  width: 50px;
  height: 50px;
  position: fixed;
  top: 3%;
  transform: translateY(-50%);
  right: 6%;
  cursor: pointer;
  z-index: 12;
  transition: background-size 0.3s ease, background-image 0.3s ease, background-color 0.3s ease;
  text-align: center;
  color: transparent;
  display: flex;
  align-items: center;
  opacity: 0.8;
}

.lightbox__close:hover {
  opacity: 1;
}

/* ==========================================================================
   ANIMATIONS
   ========================================================================== */
@keyframes lightboxIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
/* ==========================================================================
   MEDIA QUERIES
   ========================================================================== */
@media (max-width: 1440px) {
  .lightbox__next::before,
  .lightbox__prev::after {
    content: "";
  }
}
@media (max-width: 768px) {
  .lightbox__next::before {
    right: 2%;
  }
  .lightbox__prev::after {
    right: 1%;
  }
}